let CommonUtil = {
  USER_KEY: 'USER_KEY',

  USER_SISSION_KEY: 'JSESSIONID',

  ACTIVITY_KEY: 'ACTIVITY_KEY',

  LOTS_KEY: 'LOTS_KEY',

  getUser: function () {
    let json = localStorage.getItem(this.USER_KEY);
    if (json === null || json === "undefined") return null;
    return JSON.parse(json);
  },

  saveUser: function (user) {
    localStorage.setItem(this.USER_KEY, JSON.stringify(user));
  },

  saveUserSession: function (sessionId) {
    localStorage.setItem(this.USER_SISSION_KEY, sessionId);
  },

  getUserSession: function () {
    return localStorage.getItem(this.USER_SISSION_KEY);
  },

  getActivity: function () {
    let json = localStorage.getItem(this.ACTIVITY_KEY);
    if (json === null || json === "undefined") return null;
    return JSON.parse(json);
  },

  saveActivity: function (activity) {
    localStorage.setItem(this.ACTIVITY_KEY, JSON.stringify(activity));
  },

  getLot: function (index) {
    let lots = this.listLots();
    if (lots === null) return null;
    if (lots.length === 0) return null;
    return lots[index];
  },

  listLots: function () {
    let json = localStorage.getItem(this.LOTS_KEY);
    if (json === null || json === "undefined") return null;
    return JSON.parse(json);
  },

  saveLots: function (lots) {
    localStorage.setItem(this.LOTS_KEY, JSON.stringify(lots));
  },

  getNextPageKey: function (curPage, moneyCreationWay) {
    if (curPage === null || curPage.length === 0) return '/Welcome';

    let user = this.getUser();
    if (user === null || user.length === 0) return '/Welcome';

console.log(user);
    // userType: 0 ==> system, 1 ==> bidder, 2 ==> auctioneer
    if (user.userType === '1' || user.userType === 1) {
      if (curPage.endsWith('login')) return '/myDream';
      if (curPage.endsWith('myDream')) return '/receiveMoney';
      if (curPage.endsWith('receiveMoney') && moneyCreationWay === '0') return '/receiveMoneyByFixedAmount';
      if (curPage.endsWith('receiveMoney') && moneyCreationWay === '1') return '/receiveMoneyByAge';
      if (curPage.endsWith('receiveMoneyByAge')) return '/receiveMoneyByFixedAmount';
      if (curPage.endsWith('receiveMoneyByFixedAmount')) return '/auction';
      if (curPage.endsWith('auction')) return '/testimonial';
      if (curPage.endsWith('testimonial')) return '/over';
    } else if (user.userType === '2' || user.userType === 2) {
      if (curPage.endsWith('login')) return '/rules';
      if (curPage.endsWith('rules')) return '/auction';
      if (curPage.endsWith('auction')) return '/lots';
      if (curPage.endsWith('lots')) return '/testimonials';
      if (curPage.endsWith('testimonials')) return '/over';
    }

    return '/Welcome';
  }

}

export default CommonUtil;



