<template>
  <div class="card login-card card-container">
    <h5 class="card-header login-card-header">Activity Rules</h5>
    <div class="card-body">
      <ul class="rules-content">
        <li>highest offer win the lot</li><br/>
        <li>each lot has own starting price and increments, you must offer amount that greater than it,  formula is:</li><br/>
        <p><strong>offer = starting + N * increments</strong></p>
        <br/>
        <li>auctioneer decide the order of lots</li><br/>
      </ul>
    </div>
    <ToaskMessage :content='this.content' v-bind:shown='this.errorCount' />
    <div class="card-footer footer-container">
        <button type="submit" class="btn btn-primary btn-rule-next" v-on:click='this.toNext'>{{this.button_text}}</button>
    </div>
  </div>
</template>

<script>
  import qs from 'qs';
  import router from '@/router'
  import axios from '@/js/axios.js';
  import ToaskMessage from '@/components/toaskMessage.vue'
  export default {
    name: 'Rules',
    data () {
      return {
        content: 'reuqest data error!',
        errorCount: 0,
        button_text: 'START AUCTION'
      }
    },
    methods: {
      getActivity: function () {
        let me = this;
        axios.get("/api/activity/get").then(function (res) {
          // console.log(res);
          if (res.data.code !== 0) {
            me.errorContent = 'get activity faile'
            me.showCount++;
            return;
          }
          me.activity = res.data.activity;
          me.$commonUtil.saveActivity(me.activity);
          me.listLots();
        }).catch(function (err) {
          console.log(err);
          me.errorContent = 'get activity status faile'
          me.showCount++;
        })
      },
      listLots: function () {
        let me = this;
        axios.get('/api/lot/listLot').then(function (response) {
          if (response.data.code !== 0) {
            me.errorContent = 'got error when query lots!'
            me.showCount++
          } else {
            me.lotsList = qs.parse(response.data.data)
            me.$commonUtil.saveLots(me.lotsList);
          }
        }).catch(function (error) {
          console.log(error);
          me.errorContent = 'got error when query lots!'
          me.showCount++
        });
      },
      toNext: function () {
        let lot = this.$commonUtil.getLot(0);
        // lot = {};
        if (lot === null) {
          this.content = 'Lot\'s data not loaded!';
          this.errorCount++;
          return;
        }

        let me = this;
        axios.get('/api/lot/point/' + lot.id).then(function (res) {
          if (res.data.code !== 0) {
            me.content = res.data.msg;
            me.errorCount++;
            return;
          }
          // router.replace({path: '/auction'});
          let nextPage = me.$commonUtil.getNextPageKey('rules');
          router.replace({path: nextPage});
        }).catch(function (err) {
          console.log(err);
          me.content = 'starting activity occur error';
          me.errorCount++;
        })
      }
    },
    components: {
      ToaskMessage: ToaskMessage
    },
    mounted: function () {
      this.getActivity();
    }
  }
</script>

<style scoped>
  .rules-content {
    margin-top: 2rem;
    text-align: left;
  }
</style>



