// createSocket --> onOpen --> heartBit.check --> send heartBit && close socket --> reconnect -->  createSocket
import MessageUtils from '@/js/MessageUtils.js'
let WebSocketUtils = {
    socket: null,

    messages: [],

    // prevent double connect
    lockConnection: false,

    socketTimeoutObject: null,

    getUrl: function () {
        let host = window.location.host;
        let arr = host.split(":");
        return "ws://" + arr[0] + ":8886/";
    },

    init: function () {
        let me = this;
        me.socket.onopen = function () {
            if (me.messages != null && me.messages.length > 0) me.resend();
            me.heartBit.check();
        };

        me.socket.onclose = function () {
            me.reconnect(me.getUrl());
        }

        me.socket.onmessage = function (event) {
            me.messageArrival(event.data);
            me.heartBit.check();
        }

        me.socket.onerror = function () {
            me.reconnect(me.getUrl());
        }
    },

    // 触发事件, 供外部调用
    messageArrival: function (msg) {},

    reconnect: function (url) {
        let me = this;
        if (me.lockConnection) return;
        me.lockConnection = true;

        // me.socketTimeoutObject && clearTimeout(me.socketTimeoutObject);
        clearTimeout(me.socketTimeoutObject);
        me.socketTimeoutObject = setTimeout(function () {
            me.createSocket();
            me.lockConnection = false;
        }, 4000);
    },

    heartBit: {
        timeout: 3000,

        pingTimeoutObject: null,

        closeTimeoutObject: null,

        check: function () {
            let me = this;
            // this.pingTimeoutObject && clearTimeout(this.pingTimeoutObject);
            // this.closeTimeoutObject && clearTimeout(this.closeTimeoutObject);
            clearTimeout(this.pingTimeoutObject);
            clearTimeout(this.closeTimeoutObject);

            let closeSocket = function () {
                WebSocketUtils.socket.close();
            };

            let pingSocket = function () {
                WebSocketUtils.send(MessageUtils.generateHeartBeatMessage());
                me.closeTimeoutObject = setTimeout(closeSocket, me.timeout);
            };

            this.pingTimeoutObject = setTimeout(pingSocket, me.timeout);
        }
    },

    send: function (msg) {
        let me = this;
        if (me.socket.readyState === 1) {
            me.socket.send(msg);
        } else {
            me.messages.push(msg)
        }
    },

    resend: function () {
        let arr = this.messages;
        this.messages = [];
        for (let i = 0; i < arr.length; i++) {
            this.send(arr[i]);
        }
    },

    createSocket: function () {
        let me = this;
        try {
            // console.log("createSocket");
            me.socket = new WebSocket(this.getUrl());
            // console.log("createSocket_1");
            me.init();
        } catch (e) {
            me.reconnect(this.getUrl());
        }
    }
}

export default WebSocketUtils;



