<template>
  <div class="card login-card card-container">
    <h5 class="card-header login-card-header">Auction Lots</h5>
    <div class="card-body">
      <ul class="list-group">
        <li class="list-group-item d-flex justify-content-between align-items-center lots-item" v-for="item in lotsList" :key="item.id">
          <img v-bind:src="item.imageFile" alt="item picture" class="col-4 lots-item-image">
          <ul class="col-8 lots-item-info">
            <li class="row lots-item-name">{{item.name}}</li>
            <li class="row">
              <div class="col-12">
                <span class="lots-item-price">${{item.purchasePrice}}</span>
                <span>{{item.buyer}} </span>
                <span v-if="item.buyer !== null && item.buyer.length > 0">got it</span>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <ToaskMessage :content='this.content' v-bind:shown='this.errorCount' />
    <div class="card-footer footer-container">
        <button type="submit" class="btn btn-primary btn-rule-next" v-on:click='this.toNext'>NEXT</button>
    </div>
  </div>
</template>

<script>
  import qs from 'qs';
  import router from '@/router'
  import axios from '@/js/axios.js';
  import ToaskMessage from '@/components/toaskMessage.vue'
  export default {
    name: 'Lots',
    data () {
      return {
        content: 'reuqest data error!',
        errorCount: 0,
        lotsList: []
      }
    },
    methods: {
      listLots: function () {
        let me = this;
        axios.get('/api/lot/listLot').then(function (response) {
          if (response.data.code !== 0) {
            me.errorContent = 'got error when query lots!'
            me.showCount++
          } else {
            me.lotsList = qs.parse(response.data.data)
            me.$commonUtil.saveLots(me.lotsList);
          }
        }).catch(function (error) {
          console.log(error);
          me.errorContent = 'got error when query lots!'
          me.showCount++
        });
      },
      toNext: function () {
        // router.replace({path: '/testimonials'})
        let nextPage = this.$commonUtil.getNextPageKey('lots');
        router.replace({path: nextPage});
      }
    },
    components: {
      ToaskMessage: ToaskMessage
    },
    mounted: function () {
      this.listLots();
    }
  }
</script>

<style scoped>
  .lots-item {
    border-left: 0px solid rgba(0,0,0,.125);
    border-right: 0px solid rgba(0,0,0,.125);
    border-top: 0px solid rgba(0,0,0,.125);
    border-bottom: 1px solid rgba(0,0,0,.125);
    text-align: left;
  }
  .lots-item-image {
    width: 8rem;
    height: 8rem;
  }
  .lots-item-info {
    padding-top: 3rem;
    color: gray;
  }
  .lots-item-name {
    margin-left: .1rem;
  }
  .lots-item-price {
    font-size: 24px;
    font-weight: bold;
    color: black;
  }
</style>



