<template>
  <div id='receive_money' class="card  card-container">
    <div class="card-header">
      <h1>Rest Of Your Lifetime</h1>
    </div>
    <div class="card-body">
      <!-- <p>fill your age in following box:</p> -->
      <p>the years number of rest of your lifetime:</p>
      <input id='txt_age' type="number" step="1" min="0" class="form-control txt-age" name="" value="30" v-on:blur='checkAge'/>
    </div>
    <ToaskMessage :content='this.errorContent' v-bind:shown='this.showCount'/>
    <div class="card-footer footer-container">
      <button class="btn btn-primary btn-next" type="button" name="button" id='btn_money_to_next' v-on:click='this.toNext'>NEXT</button>
    </div>
  </div>
</template>

<script>
  import qs from 'qs';
  import router from '@/router'
  import axios from '@/js/axios.js';
  import ToaskMessage from '@/components/toaskMessage.vue'
  export default {
    name: 'ReceiveMoney1',
    data () {
      return {
        age: 30,
        errorContent: 'get your money faile',
        showCount: 0
      }
    },
    methods: {
      toNext: function () {
        let me = this;
        if (me.checkAge() === false) return

        let data = {};
        data.age = document.getElementById('txt_age').value

        axios.post(
          '/api/bidderMoney/create',
          qs.stringify(data),
          {headers: {'Content-Type': 'application/x-www-form-urlencoded'}}
        ).then(result => {
          let code = result.data.code;
          if (code !== 0) {
            me.errorContent = 'create money faile'
            me.showCount++;
            return;
          }

          let nextPage = me.$commonUtil.getNextPageKey('receiveMoneyByAge');
          router.replace({
            path: nextPage,
            query: {
              money: result.data.data.money
            }
          });
        }).catch(function (err) {
          console.log(err);
          me.errorContent = 'create money faile'
          me.showCount++;
        })
      },
      checkAge: function () {
        let val = document.getElementById('txt_age').value - 0
        if (val === 0 || isNaN(val)) {
          this.errorContent = 'age must be a number'
          this.showCount++
          return false
        }
        if (val < 0) {
          this.errorContent = 'age must be greater than zero'
          this.showCount++
          return false
        }
        return true
      }
    },
    components: {
      ToaskMessage: ToaskMessage
    }
  }
</script>

<style scoped>
</style>



