/* eslint-disable */
<template>
    <div class="card card-dream card-container">
      <div class="card-header">
        <span class="dream-tips">choose 3 item for your dream</span>
      </div>
      <div class="card-body">
        <div class="row dream-item" v-for="item in lotsArray" :key="item.g_options_tag">
          <select class="form-select select-expecting-item col col-30" aria-label="Default select" v-bind:id='item.gitem' v-on:change="changeSelect(item.index)">
            <option v-bind:value="itm.id" v-for="itm in lotsList" :key="itm.id" v-bind:name='item.g_options_tag'>{{itm.name}}</option>
          </select>
          <input v-bind:id='item.gprice' class="col col-60 txt-expecting-price form-control" type="number" :name="item.gprice" value="" min="0" step="1" data-bind="value:replyNumber" placeholder="expecting price">
        </div>
      </div>
      <ToaskMessage :content='this.errorContent' v-bind:shown='this.showCount'/>
      <div class="card-footer footer-container">
        <button class="btn btn-primary btn-next" type="button" name="button" id='btn_dream_to_next' v-on:click='this.toSubmit'>NEXT</button>
      </div>
    </div>
</template>

<script>
  import qs from 'qs';
  import router from '@/router'
  import axios from '@/js/axios.js';
  import ToaskMessage from '@/components/toaskMessage.vue'
  export default {
    name: 'MyDream',
    data () {
      return {
        lotsArray: [
          {index: 0, gitem: 'my_dream_0', g_options_tag: 'g0', gprice: 'my_price_0'},
          {index: 1, gitem: 'my_dream_1', g_options_tag: 'g1', gprice: 'my_price_1'},
          {index: 2, gitem: 'my_dream_2', g_options_tag: 'g2', gprice: 'my_price_2'}
        ],
        lotsList: [],
        errorContent: 'got error when query lots!',
        showError: false,
        showCount: 0,
        activity: {}
      }
    },
    methods: {
      changeSelect: function (index) {
        if (index === 0) {
          document.getElementById(this.lotsArray[1].gitem).value = null
          document.getElementById(this.lotsArray[2].gitem).value = null
          this.showElement(this.lotsArray[1].g_options_tag);
          this.showElement(this.lotsArray[2].g_options_tag);
          let val0 = document.getElementById(this.lotsArray[0].gitem).value
          let val1 = document.getElementById(this.lotsArray[1].gitem).value
          let val2 = document.getElementById(this.lotsArray[2].gitem).value
          this.hideSelection(this.lotsArray[1].g_options_tag, val0, val2)
          this.hideSelection(this.lotsArray[2].g_options_tag, val0, val1)
        } else if (index === 1) {
          document.getElementById(this.lotsArray[2].gitem).value = null
          this.showElement(this.lotsArray[2].g_options_tag);
          let val0 = document.getElementById(this.lotsArray[0].gitem).value
          let val1 = document.getElementById(this.lotsArray[1].gitem).value
          this.hideSelection(this.lotsArray[2].g_options_tag, val0, val1)
        } else if (index === 2) {
          // no need to change as last selection
        }
      },
      getActivity: function () {
        let me = this;
        axios.post("/api/activity/get").then(function (res) {
          if (res.data.code !== 0) {
            me.errorContent = 'get activity faile'
            me.showCount++;
            return;
          }
          me.activity = res.data.data;
          me.$commonUtil.saveActivity(me.activity);
          me.listLots(me.activity.id);
        }).catch(function (err) {
          console.log(err);
          me.errorContent = 'get activity status faile'
          me.showCount++;
        })
      },
      listLots: function (activityId) {
        let me = this;
        axios.post('/api/lot/list/' + activityId).then(function (response) {
          if (response.data.code !== 0) {
            me.errorContent = 'got error when query lots!'
            me.showCount++
          } else {
            me.lotsList = qs.parse(response.data.data)
            me.$commonUtil.saveLots(me.lotsList);
            setTimeout(function () {
              me.changeSelect(0)
            }, 1000)
          }
        }).catch(function (error) {
          console.log(error);
          me.errorContent = 'got error when query lots!'
          me.showCount++
        });
      },
      showElement: function (gName) {
        let arrEle = document.getElementsByName(gName);
        for (var index in arrEle) {
          let ele = arrEle[index];
          if (typeof (ele) !== 'object') continue
          ele.style.display = 'block'
        }
      },
      hideSelection: function (gName, selId1, selId2) {
        this.hideSelected(gName, selId1);
        this.hideSelected(gName, selId2);
      },
      hideSelected: function (gName, selId) {
        if (gName === undefined) return;
        if (selId === undefined) return;

        let arrEle = document.getElementsByName(gName);
        for (var index in arrEle) {
          let ele = arrEle[index];
          if (typeof (ele) !== 'object') continue
          if (ele.value !== selId) continue
          ele.style.display = 'none'
        }
      },
      toSubmit: function () {
        let lots = [];
        for (var idx in this.lotsArray) {
          // console.log('g', g)
          let lot = this.getBidderExpectingLot(idx);
          if (lot == null) continue
          if (lot.lotId === null || lot.lotId.length === 0) break;
          if (lot.expectingPrice === null || lot.expectingPrice.length === 0) {
            this.errorContent = "A dream's expecting price cannot be null";
            this.showCount++;
            // console.log('this.showCount', this.showCount);
            return;
          }

          if (isNaN(lot.expectingPrice - 0)) {
            this.errorContent = "A dream's expecting price must be a number";
            this.showCount++;
            return;
          }

          if ((lot.expectingPrice - 0) < 0) {
            this.errorContent = "A dream's expecting price cannot less than zero";
            this.showCount++;
            return;
          }

          lots.push(lot);
        }

        console.log('lots:');
        console.log(lots);
        if (lots === undefined || lots.length === 0 || lots[0].lotId === undefined || lots[0].lotId.length === 0) {
          this.errorContent = 'you must choose a dream'
          this.showCount++;
          return
        }

        let data = {};
        let user = this.$commonUtil.getUser();
        data.bidderId = user.id;
        data.lots = JSON.stringify(lots);

        console.log('lots Json:');
        console.log(data.lots);

        let me = this;
        axios.post(
          '/api/lot/new/expectLots',
          qs.stringify(data),
          {headers: {'Content-Type': 'application/x-www-form-urlencoded'}}
        ).then(result => {
          let code = result.data.code;
          // console.log(result.data);
          if (code !== 0) {
            me.errorContent = 'save your dream faile!'
            me.showCount++;
            return;
          }

          let nextPage = me.$commonUtil.getNextPageKey('myDream');
          router.replace({
            path: nextPage,
            query: {
              money: me.activity.money,
              moneyCreationWay: me.activity.moneyCreationWay,
              amountPerAge: me.activity.amountPerAge
            }
          });
        }).catch(function (err) {
          console.log(err);
          me.errorContent = 'save your dream faile!'
          me.showCount++;
        })
      },
      getBidderExpectingLot: function (index) {
        if (index < 0) return null
        let lot = {}
        // console.log(this.lotsArray, index);
        lot.lotId = document.getElementById(this.lotsArray[index].gitem).value - 0
        lot.expectingPrice = document.getElementById(this.lotsArray[index].gprice).value - 0
        return lot
      }
    },
    components: {
      ToaskMessage: ToaskMessage
    },
    mounted () {
      // this.listLots();
      this.getActivity();
    }
  }
</script>

<style scoped>
  .select-expecting-item {
    margin-top: 1rem;
  }
  .btn-next {
    width: 100%;
  }
  .txt-expecting-price {
    margin-top: .9rem;
    margin-left: .5rem;
  }
</style>



