<template>
  <div class="card card-container">
    <h5 class="card-header">Activity Testimonials</h5>
    <div class="card-body">
      <div class="testi-container">
        <div class="row testi-item" v-for="item in testimonials" :key="item.id">
          <span class="col-xs-9 col-sm-10 testi-item-content">{{item.content}}</span>
          <span class="col-xs-3 col-sm-2  testi-item-bidder">{{item.bidder}}</span>
        </div>
      </div>
    </div>
    <ToaskMessage :content='this.errorContent' v-bind:shown='this.showCount' />
    <div class="card-footer footer-container">
        <button type="submit" class="btn btn-primary btn-rule-next" v-on:click='this.toNext'>NEXT</button>
    </div>
  </div>
</template>

<script>
  import qs from 'qs';
  import router from '@/router'
  import axios from '@/js/axios.js';
  import ToaskMessage from '@/components/toaskMessage.vue'
  export default {
    name: 'testimonials',
    data () {
      return {
        errorContent: 'reuqest data error!',
        showCount: 0,
        testimonials: [
        ]
      }
    },
    methods: {
      listTestimonials: function () {
        let me = this;
        let activity = me.$commonUtil.getActivity();
        if (activity === null) {
          me.errorContent = 'activity data did not loaded';
          me.showCount++;
          return;
        }
        // if (activity == null) activity = {};
        axios.get('/api/testimonials/' + activity.id).then(function (response) {
          if (response.data.code !== 0) {
            me.errorContent = 'got error when query testimonials!';
            me.showCount++;
          } else {
            me.testimonials = qs.parse(response.data.data);
          }
        }).catch(function (error) {
          console.log(error);
          me.errorContent = 'got error when query testimonials!';
          me.showCount++;
        });
      },
      toNext: function () {
        // router.replace({path: '/over'})
        let nextPage = this.$commonUtil.getNextPageKey('testimonials');
        router.replace({path: nextPage});
      }
    },
    components: {
      ToaskMessage: ToaskMessage
    },
    mounted: function () {
      this.listTestimonials();
    }
  }
</script>

<style scoped>
  .testi-item {
    border-top: 0px solid rgba(0,0,0,.125);
    border-right: 0px solid rgba(0,0,0,.125);
    border-bottom: 1px solid rgba(0,0,0,.125);
    border-left: 0px solid rgba(0,0,0,.125);
    padding-top: .5rem;
    padding-bottom: .5rem;
    text-align: left;
  }
  .testi-item-bidder {
    color: #ccc;
    padding-top: .2rem;
    text-align: right;
  }
</style>



