<template>
  <div id='receive_money' class="card  card-container">
    <div class="card-body">
      <p>wait a minute please</p>
    </div>
    <ToaskMessage :content='this.errorContent' v-bind:shown='this.showCount'/>
  </div>
</template>

<script>
  import router from '@/router'
  import ToaskMessage from '@/components/toaskMessage.vue'
  export default {
    name: 'ReceiveMoney',
    data () {
      return {
        errorContent: 'query data error',
        showCount: 0
      }
    },
    methods: {
      toNext: function () {
        let me = this;
        let money = me.$route.query.money;
        let amountPerAge = me.$route.query.amountPerAge;
        let moneyCreationWay = me.$route.query.moneyCreationWay;

        // moneyCreationWay: 0 ==> fixed amount, 1 ==> by age
        // if (moneyCreationWay === '0') {
        //   router.replace({
        //     path: '/receiveMoney2',
        //     query: {
        //       money: money
        //     }
        //   });
        // } else {
        //   router.replace({
        //     path: '/receiveMoney1',
        //     query: {
        //       amountPerAge: amountPerAge
        //     }
        //   });
        // }

        let nextPage = this.$commonUtil.getNextPageKey('receiveMoney', moneyCreationWay);
        router.replace({
          path: nextPage,
          query: {
            money: money,
            amountPerAge: amountPerAge
          }
        });
      }
    },
    components: {
      ToaskMessage: ToaskMessage
    },
    mounted: function () {
      let me = this;
      setTimeout(function () {
        me.toNext();
      }, 2000);
    }
  }
</script>

<style scoped>
</style>



