/* eslint-disable */
import Vue from 'vue'
import Router from 'vue-router'

// bidder's page
import MyDream from '@/components/MyDream.vue'
import ReceiveMoney from '@/components/receiveMoney.vue'
import ReceiveMoneyByAge from '@/components/receiveMoneyByAge.vue'
import ReceiveMoneyByFixedAmount from '@/components/receiveMoneyByFixedAmount.vue'
import Testimonial from '@/components/testimonial.vue'

// auctioneer's page
import Rules from '@/components/auctioneer/rule.vue'
import Lots from '@/components/auctioneer/lots.vue'
import Testimonials from '@/components/auctioneer/testimonials.vue'

// public page
import Welcome from '@/components/welcome.vue'
import Login from '@/components/login.vue'
import Auction from '@/components/auction.vue'
import Over from '@/components/over.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'welcome',
      component: Welcome
    }, {
      path: '/login',
      name: 'login',
      component: Login
    }, {
      path: '/myDream',
      name: 'MyDream',
      component: MyDream
    }, {
      path: '/receiveMoney',
      name: 'ReceiveMoney',
      component: ReceiveMoney
    }, {
      path: '/receiveMoneyByAge',
      name: 'ReceiveMoneyByAge',
      component: ReceiveMoneyByAge
    }, {
      path: '/receiveMoneyByFixedAmount',
      name: 'ReceiveMoneyByFixedAmount',
      component: ReceiveMoneyByFixedAmount
    }, {
      path: '/auction',
      name: 'Auction',
      component: Auction
    }, {
      path: '/testimonial',
      name: 'Testimonial',
      component: Testimonial
    }, {
      path: '/over',
      name: 'Over',
      component: Over
    }, {
      path: '/rules',
      name: 'Rules',
      component: Rules
    }, {
      path: '/lots',
      name: 'Lots',
      component: Lots
    }, {
      path: '/testimonials',
      name: 'Testimonials',
      component: Testimonials
    }
  ]
})



