<template>
  <div id='home'>
    <h1>to find your dream, find your future</h1>
    <ToaskMessage :content='this.content' v-bind:shown='this.errorCount' />
  </div>
</template>

<script>
  import router from '@/router'
  import axios from '@/js/axios.js';
  import ToaskMessage from '@/components/toaskMessage.vue'
  export default {
    name: 'Welcome',
    data () {
      return {
        content: 'reuqest data error!',
        errorCount: 0
      }
    },
    methods: {
      needToLogin: function () {
        let self = this;
        axios.get('/api/activity/needLogin').then(function (response) {
          // console.log(response)
          if (response.data.data.needLogin === true) {
            router.replace({path: '/login'});
          } else {
            router.replace({path: '/myDream'});
          }
        }).catch(function (error) {
          console.log(error);
          self.errorCount++
        });
      }
    },
    components: {
      ToaskMessage: ToaskMessage
    },
    mounted: function () {
      let me = this;
      setTimeout(function () {
        me.needToLogin();
      }, 3000);
    }
  }
</script>

<style scoped>
  #home {
    display: table;
    width: 100%;
    height:300px;
    margin: 0 auto;
  }
  #home h1{
    display: table-cell;
    vertical-align:middle;
  }
</style>



