<template>
  <div class="card login-card card-container">
    <h5 class="card-header login-card-header">login please</h5>
    <div class="card-body">
        <div class="input-group mb-3">
          <i class="bi bi-file-person-fill input-group-text" id="login-account"><span class='required'>*</span></i>
          <input id='txt_account' type="text" class="form-control" placeholder="username" aria-label="Username" aria-describedby="login-account">
        </div>

        <div class="input-group mb-3">
          <i class="bi bi-file-lock-fill input-group-text" id="login-password">&nbsp;</i>
          <input id='txt_password' type="password" class="form-control" placeholder="password" aria-label="Password" aria-describedby="login-password">
        </div>
    </div>
    <ToaskMessage :content='this.content' v-bind:shown='this.errorCount' />
    <div class="card-footer footer-container">
        <button type="submit" class="btn btn-primary btn-login" v-on:click='this.toSubmit'>LOGIN</button>
    </div>
  </div>
</template>

<script>
  import qs from 'qs';
  import router from '@/router'
  import axios from '@/js/axios.js';
  import ToaskMessage from '@/components/toaskMessage.vue'
  export default {
    name: 'Login',
    data () {
      return {
        content: 'reuqest data error',
        errorCount: 0
      }
    },
    methods: {
      toSubmit: function () {
        let data = {};
        data.username = document.getElementById("txt_account").value;
        data.password = document.getElementById("txt_password").value;

        if (data.username === null || data.username.length === 0) {
          this.content = 'username cannot be null'
          this.errorCount++;
          document.getElementById("txt_account").focus();
          return;
        }
  // console.log('begin to request');
        let me = this;
        axios.post(
          '/api/user/login',
          qs.stringify(data),
          {headers: {'Content-Type': 'application/x-www-form-urlencoded'}}
        ).then(result => {
          // console.log('result:', result);
          let code = result.data.code;
          if (code !== 0) {
            me.content = 'login faile!'
            me.errorCount++;
            return;
          }
          let sessionId = result.headers.jsessionid;
          me.$commonUtil.saveUser(result.data.data);
          me.$commonUtil.saveUserSession(sessionId);
          let nextPage = me.$commonUtil.getNextPageKey('login')
          router.replace({path: nextPage});
        }).catch(function (err) {
          console.log(err);
          me.content = 'post submit data occur error';
          me.errorCount++;
        });
      }
    },
    components: {
      ToaskMessage: ToaskMessage
    }
  }
</script>

<style scoped>
  /* .login-card-header {
    text-align: left;
  } */
  .btn-login {
    width: 100%;
  }
</style>



