 <template>
   <div class="alert alert-warning alert-dismissible fade show" role="alert" id='iToask' v-show="this.visible">
     {{content}}
     <button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
   </div>
</template>

<script>
  export default {
    name: 'ToaskMessage',
    data () {
      return {
        visible: false
      }
    },
    computed: {
      isShown: function () {
        return ((this.count % 2) === 0)
      }
    },
    props: {
      header: {type: String, default: 'Title'},
      content: {type: String, default: "here is content"},
      shown: {type: Number, default: 0}
    },
    methods: {
      autoClosed: function () {
        let me = this
        setTimeout(function () {
          me.visible = false;
        }, 3000)
      }
    },
    watch: {
      'shown': function (val, oval) {
        // console.log('msg shown', val);
        this.visible = true
        this.autoClosed();
      }
    }
  }
</script>

<style scoped>
</style>



