<template>
  <div id='receive_money' class="card  card-container">
    <div class="card-header">
      <h1>your money</h1>
    </div>
    <div class="card-body money_tips">
      <h1>you got <strong> ${{this.amount}}</strong></h1>
      <p>It represent All your time and energy</p>
      <p>You will use it in the auction activity</p>
    </div>
    <ToaskMessage :content='this.errorContent' v-bind:shown='this.showCount'/>
    <div class="card-footer footer-container">
      <button class="btn btn-primary btn-next" type="button" name="button" id='btn_money_to_next' v-on:click='this.toNext'>NEXT</button>
    </div>
  </div>
</template>

<script>
  import router from '@/router'
  import axios from '@/js/axios.js';
  import ToaskMessage from '@/components/toaskMessage.vue'
  export default {
    name: 'ReceiveMoney2',
    data () {
      return {
        amount: 0,
        errorContent: 'get your money faile',
        showCount: 0
      }
    },
    props: {
      money: {type: Number, default: 0}
    },
    methods: {
      getMoney: function () {
        let money = this.$route.query.money
        if (money === undefined || money.length === 0) {
          this.errorContent = 'get your money faile'
          this.showCount++;
          return;
        }
        this.amount = money;
      },
      toNext: function () {
        let me = this;
        axios.get("/api/activity/get").then(function (res) {
          // console.log(res);
          if (res.data.code !== 0) {
            me.errorContent = 'get activity status faile'
            me.showCount++;
            return;
          }
          if (res.data.activity.state < 4) {
            me.errorContent = ' waiting for aucton begining'
            me.showCount++;
            return;
          }
          me.$commonUtil.saveActivity(res.data.activity);
          // router.replace({path: '/auction'});
          let nextPage = me.$commonUtil.getNextPageKey('receiveMoneyByFixedAmount');
          router.replace({path: nextPage});
        }).catch(function (err) {
          console.log(err);
          me.errorContent = 'get activity status faile'
          me.showCount++;
        })
      }
    },
    components: {
      ToaskMessage: ToaskMessage
    },
    mounted: function () {
      this.getMoney();
    }
  }
</script>

<style scoped>
  .money_tips {
    padding:40px 0;
  }
</style>



