<template>
  <div id='auction_lots' class="card  card-container">
    <div id='lots-image' class="lots-img" :style="{backgroundImage: 'url(' + pic + ')'}"></div>
    <ul class="bid-list">
      <li v-for="item in bidList" :key="item.id">{{item.name}}: {{item.amount}}</li>
    </ul>
    <div class="card-body">
      <div class="row">
        <div class="col col-7 auction-item">
          <div class="row lot-item-name"><strong>{{this.lot.name}}</strong></div>
          <div class="row">
            <div class="col-6"><span class="d-none d-sm-block">Start:</span>{{this.lot.startingPrice}}</div>
            <div class="col-6"><span class="d-none d-sm-block">increment:</span> {{this.lot.increment}}</div>
          </div>
        </div>
        <div class="col col-5 btn-group auction-action" v-if="this.isBidder">
          <input type="number" class="form-control"  id='txt_offer_price' :value="this.bidderOfferPrice" :min='this.lot.startingPrice' :step='this.lot.increment'>
          <button type="button" class="btn btn-dark" id="btn-incremet" v-on:click="increment">+</button>
        </div>
      </div>
    </div>
    <ToaskMessage :content='this.errorContent' v-bind:shown='this.showCount'/>
    <div class="card-footer footer-container">
      <button class="btn btn-primary btn-next" type="button" name="button" id='btn_auction_offer' v-on:click='this.toOffer' v-if="this.isBidder">OFFER</button>
      <button class="btn btn-primary btn-next" type="button" name="button" id='btn_auction_next' v-on:click='this.toNext' v-else>NEXT</button>
    </div>
  </div>
</template>

<script>
  import qs from 'qs';
  import router from '@/router'
  import axios from '@/js/axios.js';
  import Pic from '@/assets/zyt.jpg'
  import ToaskMessage from '@/components/toaskMessage.vue'
  export default {
    name: 'AuctionLots',
    data () {
      return {
        errorContent: 'netword error',
        showCount: 0,
        pic: Pic,
        lot: {id: 0, name: 'the best wife in the world', startingPrice: 156000, increment: 1000, imageFile: ''},
        bidList: [{id: 0, name: '赵海米', amount: 1600000}],
        bidderOfferPrice: 0,
        activity: {},
        // userType: 1,
        lotIndex: 0
      }
    },
    computed: {
      // isBidder: function () {
      //   let user = this.$commonUtil.getUser();
      //   this.userType = user.userType;
      //   return (this.userType === 1 || this.userType === '1');
      // }
      isBidder: function () {
        let user = this.$commonUtil.getUser();
        return (user.userType === 1 || user.userType === '1');
      }
    },
    methods: {
      toOffer: function () {
        let val = document.getElementById('txt_offer_price').value - 0;
        if (isNaN(val) || val < this.lot.startingPrice) {
          this.errorContent = 'invalid input'
          this.showCount++;
          return;
        }

        let user = this.$commonUtil.getUser();

        let me = this;
        let data = {};
        data.activityId = this.activity.id;
        data.bidderId = user.id;
        data.lotId = this.lot.id;
        data.price = val;
        axios.post(
          '/api/bidLog/new',
          qs.stringify(data),
          {headers: {'Content-Type': 'application/x-www-form-urlencoded'}}
        ).then(res => {
          me.errorContent = 'bid success'
          if (res.data.code !== 0) me.errorContent = res.data.msg
          me.showCount++;
        }).catch(function (err) {
          console.log(err);
          me.errorContent = 'occur error when post offer price'
          me.showCount++;
        })
      },
      toNext: function () {
        let nextIndex = this.lotIndex + 1;
        let lot = this.$commonUtil.getLot(nextIndex);
        // console.log(lot);
        // lot = {};
        if (lot === null || lot === undefined) {
          let lotList = this.$commonUtil.listLots();
          console.log(lotList, lotList.length, nextIndex);
          if (lotList !== null && (lotList.length === undefined || lotList.length <= nextIndex)) {
            let nextPage = this.$commonUtil.getNextPageKey('auction');
            router.replace({path: nextPage});
            return;
          } else {
            this.errorContent = 'Lot\'s data not loaded';
            this.showCount++;
            return;
          }
        }

        let me = this;
        axios.get('/api/lot/point/' + lot.id).then(function (res) {
          if (res.data.code !== 0) {
            me.errorContent = res.data.msg;
            me.showCount++;
            return;
          }
          me.lotIndex = nextIndex;
          me.onChangeLot(me.lotIndex);
        }).catch(function (err) {
          console.log(err);
          me.errorContent = 'get next Lot\'s data occur error';
          me.showCount++;
        });
      },
      increment: function () {
        let val = document.getElementById('txt_offer_price').value - 0;
        if (isNaN(val) || val < this.lot.startingPrice) {
          this.errorContent = 'invalid input'
          this.showCount++;
          return;
        }
        this.bidderOfferPrice = this.bidderOfferPrice + this.lot.increment
      },
      initData: function () {
        this.activity = this.$commonUtil.getActivity();
        this.onChangeLot(this.lotIndex);
        // this.bidList = [];
        this.$messageUtils.init(); // start websocket
        this.$messageUtils.onChangeLot = this.onChangeLot;
        this.$messageUtils.onAuctionOver = this.onAuctionOver;
      },
      onChangeLot: function (id) {
        this.lot = this.$commonUtil.getLot(id);
        if (this.lot == null) this.lot = {};
        this.pic = this.lot.imageFile;
        this.bidderOfferPrice = this.lot.startingPrice + this.lot.increment;
      },
      onAuctionOver: function () {
        // router.replace({path: '/testimonial'});
        let nextPage = this.$commonUtil.getNextPageKey('auction');
        router.replace({path: nextPage});
      }
    },
    components: {
      ToaskMessage: ToaskMessage
    },
    mounted: function () {
      this.initData();
    }
  }
</script>

<style scoped>
  .lots-img {
    overflow: hidden;
    background-position: center;
    background-repeat:  no-repeat;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    height: 80%;
  }
  .bid-list {
    position: absolute;
    width: 11rem;
    background-color:  rgba(255, 255, 255, 0.2);
    text-align: left;
    max-height: 5rem;
    overflow-y: scroll;
    overflow-x: hidden;
  }
  .auction-item {
    text-align: left;
  }
  .lot-item-name {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  /* .auction-action {
    text-align: right;
  } */
  #btn-incremet {
    left: -2rem;
    width: 3rem;
  }
</style>



