<template>
  <div id='auction_lots' class="card  card-container">
    <div class="card-header">
      Summary
    </div>
    <div class="card-body">
      <div class="form-floating testimonials-form">
        <textarea class="form-control testimonials-content" placeholder="Leave a comment here" id="txt_testimonials"></textarea>
        <label for="floatingTextarea">what were you expected and what's the result?</label>
      </div>
    </div>
    <ToaskMessage :content='this.errorContent' v-bind:shown='this.showCount'/>
    <div class="card-footer footer-container">
      <button class="btn btn-primary" type="button" name="button" id='btn_testimonials_submit' v-on:click='this.toSubmit'>SUBMIT</button>
    </div>
  </div>
</template>

<script>
  import qs from 'qs';
  import router from '@/router'
  import axios from '@/js/axios.js';
  import ToaskMessage from '@/components/toaskMessage.vue'
  export default {
    name: 'Testimonial',
    data () {
      return {
        errorContent: 'netword error',
        showCount: 0
      }
    },
    methods: {
      toSubmit: function () {
        let content = document.getElementById('txt_testimonials').value;
        if (content == null || content.length === 0) {
          document.getElementById('txt_testimonials').focus();
          return;
        }

        let user = this.$commonUtil.getUser();
        let activity = this.$commonUtil.getActivity();
        let data = {};
        data.activityId = activity.id;
        data.bidderId = user.id;
        data.testimonials = content;

        let me = this;
        axios.post(
          '/api/testimonials/new',
          qs.stringify(data),
          {headers: {'Content-Type': 'application/x-www-form-urlencoded'}}
        ).then(res => {
          if (res.data.code !== 0) {
            me.errorContent = res.data.msg;
            me.showCount++;
            return;
          }
          // router.replace({path: '/over'});
          let nextPage = me.$commonUtil.getNextPageKey('testimonial');
          router.replace({path: nextPage});
        }).catch(function (err) {
          console.log(err);
          me.errorContent = 'occur error when post data'
          me.showCount++;
        });
      }
    },
    components: {
      ToaskMessage: ToaskMessage
    },
    mounted: function () {
    }
  }
</script>

<style scoped>
  .testimonials-form {
    height: 100%;
  }
  .testimonials-content {
    min-height: 80%;
  }
</style>



